


















































import { Component, Vue } from 'vue-property-decorator'
import Arrows from '@/components/Arrows.vue'

type Tier = {
  name: string;
  price: number | null;
  description?: string;
  contractors: number;
  support: string;
  buttonText: string;
  to: {
    name: string;
    params: {
      [key: string]: string;
    };
  };
}

@Component({
  metaInfo: {
    title: "Pricing",
  },
  components: {
    Arrows
  }
})
export default class Pricing extends Vue {
  pricingTiers: Tier[] = [
    {
      price: 0,
      name: 'free',
      description: 'Free forever',
      contractors: 10,
      support: 'Free tier chat assistance',
      buttonText: 'Get free',
      to: {name: 'signUp', params: { subscriptionTier: 'free' }}
    },
    {
      price: 100,
      name: 'premium',
      contractors: 100,
      support: 'Standard support',
      buttonText: 'Get premium',
      to: {name: 'signUp', params: { subscriptionTier: 'premium' }}
    },
    {
      price: null,
      name: 'enterprise',
      description: 'Speak with sales',
      contractors: Infinity,
      support: '24/7 support',
      buttonText: 'Contact sales',
      to: {name: 'contact', params: { option: 'sales' }}
    }
  ]

  highlight(tier: Tier) {
    return tier.name == "premium"
  }
}
