















































import { Component, Vue, Prop } from 'vue-property-decorator'
import Breadcrumbs from '@/layouts/Breadcrumbs.vue'

@Component({
  components: {
    Breadcrumbs,
  },
})
export default class Toolbar extends Vue {
  @Prop({ default: false }) drawer!: boolean

  menu = false

  links = [
    {
      text: 'Home',
      to: '/',
      mobileOnly: true,
    },
    {
      text: 'About',
      to: 'about',
    },
    {
      text: 'Contact us',
      to: 'contact',
    },
    // {
    //   text: "Support",
    //   to: "support",
    // },
    {
      text: 'Pricing',
      to: 'pricing',
    },
    {
      text: 'Sign in',
      to: 'sign-in',
      mobileOnly: true,
    },
    {
      text: 'Sign up',
      to: 'sign-up',
      mobileOnly: true,
    },
  ]
}
