
























import { Shift } from '@/definitions/Job';
import { Vue, Component, Prop } from 'vue-property-decorator'

@Component
export default class DeleteShiftDialog extends Vue {
  loading = false

  @Prop({ default: false }) readonly opened!: boolean
  @Prop(Object) readonly shift: Shift | undefined
  @Prop(String) readonly contractorName: string | undefined

  closeDialog() {
    this.$emit("update:opened", false);
  }
  
  async deleteShift() {
    this.loading = true
    if (this.shift) {
      try {
        await this.$store.dispatch("deleteShift", {
          shiftId: this.shift.id,
          jobId: this.$route.params.jobId
        });
        this.closeDialog();
      }
      finally {
        this.loading = false
      }
    }
  }
}
